<template>
  <div fill-height>
    <v-row>
      <v-col cols="4">
        <v-card id="test_div" class="col-item-4 chart-div ma-0 pa-0"></v-card>
      </v-col>
      <v-col cols="4">
        <v-card class="pa-2 col-item" outlined tile><v-card-text>ddd</v-card-text></v-card>
      </v-col>
      <v-col cols="4">
        <v-card class="pa-2 col-item" outlined tile> </v-card>
      </v-col>
    </v-row>
    <v-row>
      <v-col cols="4">
        <v-card class="pa-2 col-item-4" outlined tile> </v-card>
      </v-col>
      <v-col cols="4">
        <v-card class="pa-2 col-item" outlined tile> </v-card>
      </v-col>
      <v-col cols="4">
        <v-card class="pa-2 col-item" outlined tile> </v-card>
      </v-col>
    </v-row>
    <v-row>
      <v-col cols="4">
        <v-card class="pa-2 col-item-4" outlined tile> </v-card>
      </v-col>
      <v-col cols="4">
        <v-card class="pa-2 col-item" style="height: 150px" outlined tile>
        </v-card>
      </v-col>
      <v-col cols="4">
        <v-card class="pa-2 col-item" outlined tile> </v-card>
      </v-col>
    </v-row>
  </div>
</template>

<script>
import { LayoutService, TYPE_HEIGHT } from "../common/layout-service";
const PageRows = 12;
const layoutService = new LayoutService();

export default {
  name: "SubTestPage2",
  components: {},
  data() {
    return {
      datacollection: null,
      option: {
        xAxis: {
          type: "category",
          data: ["Mon", "Tue", "Wed", "Thu", "Fri", "Sat", "Sun"],
        },
        yAxis: {
          type: "value",
        },
        series: [
          {
            data: [120, 200, 150, 80, 70, 110, 130],
            type: "bar",
          },
        ],
      },
    };
  },
  mounted() {
    this.initLayout();
    this.fillData();
  },
  methods: {
    initLayout() {
      let containerHeight =
        window.innerHeight -
        document.getElementsByClassName("v-toolbar")[0].offsetHeight -
        12 * 8;
      layoutService.initLayout(
        "col-item",
        containerHeight,
        PageRows,
        TYPE_HEIGHT
      );
    },
    fillData() {
      let ec = this.$echarts.init(document.getElementById("test_div"), "light");
      ec.setOption(this.option);
    },
  },
};
</script>

<style>
.small {
  max-width: 600px;
  margin: 150px auto;
}
.chart-div {
  height: 100%;
  width: 100%;
}
</style>
