import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    userInfo: {},
    isLogin: false,
  },
  getters: {
    getUserInfo: state => {
      return state.userInfo;
    },
    getLoginStatus: state => {
      return state.isLogin;
    }
  },
  mutations: {
    setUserInfo (state, userInfo) {
      state.userInfo = userInfo;
    },
    setLoginStatus (state, login) {
      state.isLogin = login;
    }
  },
  actions: {
  },
  modules: {
  }
})
