import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Login from '../views/Login.vue'
import DataStatisticPage from '../views/DataStatisticPage.vue'
import TestPage1 from '../views/TestPage1.vue'
import TestPage2 from '../views/TestPage2.vue'
import TestPage3 from '../views/TestPage3.vue'
import SubTestPage1 from '../views/SubTestPage1.vue'
import SubTestPage2 from '../views/SubTestPage2.vue'
import SubTestPage3 from '../views/SubTestPage3.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/Login',
    name: 'Login',
    component: Login
  },
  {
    path: '/testpage1',
    name: 'TestPage1',
    component: TestPage1,
    children: [
      {
        path: 'DataStatisticPage',
        component: DataStatisticPage,
      },
      {
        path: 'SubTestPage1',
        component: SubTestPage1,
      },
      {
        path: 'SubTestPage2',
        component: SubTestPage2
      },
      {
        path: 'SubTestPage3',
        component: SubTestPage3
      }
    ]
  },
  {
    path: '/testpage2',
    name: 'TestPage2',
    component: TestPage2
  },
  {
    path: '/testpage3',
    name: 'TestPage3',
    component: TestPage3
  },
  {
    path: '/about',
    name: 'About',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
